import { Component, OnInit, Output, EventEmitter, ViewEncapsulation } from '@angular/core';

import { Observable, BehaviorSubject, Subscription, Subject } from 'rxjs';
import { filter } from 'rxjs/operators';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'enquiry-source',
  templateUrl: './enquiry-source.component.html',
  styleUrls: ['./enquiry-source.component.scss']
})
export class EnquirySourceComponent implements OnInit {

  constructor(

  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this._sourcesList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

   }

  isExpanded:boolean = false;

  // Private
  private _unsubscribeAll: Subject<any>;

  isDataLoaded:boolean = true;

  programData:string = "";

  _sourcesList:any;
  fullSourcesList:any = [];

  searchData:FormControl;

  search:string = '';
  apiSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  @Output() newEnquirySourceEvent = new EventEmitter<boolean>();
  @Output() editEnquirySourceEvent = new EventEmitter<any>();

  get sources$(): Observable<any>{
    return this._sourcesList.asObservable();
  }

  addEnquirySource(event) {
    event.stopPropagation();
    this.newEnquirySourceEvent.next(true)
  }

  editEnquirySource(event, id) {
    event.stopPropagation();
    this.editEnquirySourceEvent.next(id)
  }

  ngOnInit(): void {

    // Load sources
    let res = [
      {
        "id": 107,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Search Engine",
        "description": "some search engine related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 108,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Referral",
        "description": "some referral related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      },
      {
        "id": 109,
        "topClientId": 3,
        "programTypeId": 322,
        "name": "Newspaper",
        "description": "some news paper change related description",
        "active": true,
        "lastModified": "2021-02-01T13:44:13.932Z",
        "modifiedBy": 0
      }

    ];

    this.fullSourcesList = res;
    this.allData = res;
    this._sourcesList.next(res);

    this.sources$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    });

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }

}
