import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { EnquirySetupComponent } from './enquiry-setup.component';
import { AddEnquiryTypeComponent } from './enquiry-type/add-enquiry-type/add-enquiry-type.component';
import { ViewEnquiryTypeComponent } from './enquiry-type/view-enquiry-type/view-enquiry-type.component';

import { AddEnquiryModeComponent } from './enquiry-mode/add-enquiry-mode/add-enquiry-mode.component';
import { AddEnquirySourceComponent } from './enquiry-source/add-enquiry-source/add-enquiry-source.component';
import { EnquirySetupResolver } from './enquiry-setup.resolver';

const routes: Routes = [
  { path: '', component: EnquirySetupComponent, canActivate: [AuthGuard],
    children: [
      {
        path         : 'type/:type/:id',
        component    : AddEnquiryTypeComponent,
        resolve: {
          EnquirySetupResolver
        }
      },
      {
        path         : 'mode/:type/:id',
        component    : AddEnquiryModeComponent,
        resolve: {
          EnquirySetupResolver
        }
      },
      {
        path         : 'source/:type/:id',
        component    : AddEnquirySourceComponent,
        resolve: {
          EnquirySetupResolver
        }
      },
      {
        path: "type-view/:id",
        component: ViewEnquiryTypeComponent,
        resolve: {
          EnquirySetupResolver
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EnquirySetupRoutingModule { }
